var that;
export default {
	data() {
		return {
			loading:false,
			userlist: [],
			page: 1,
			nomore: false,
			nolist: false
		};
	},
	methods: {
		del(e) {
			let id = e.currentTarget.dataset.id; 
			let index=e.currentTarget.dataset.index;
			uni.showModal({
				title: '提示',
				content: '确定要删除',
				cancelText: '取消',
				confirmText: '确定',
				success(res) {
					if (res.confirm) {
						that.$request
							.request({
								url: that.api.live.del_notice_live,
								data: {
									id: id
								},
								Toast:'请稍等...',
								method:"get"
							})
							.then(res => { 
								that.userlist.splice(index,1)
								if(that.userlist.length<5){
									that.page=1;
									that.getlist()
								}
								that.$alertok("删除成功！");
								
							});
					}
				}
			});
		},
		getlist() {
			that.loading=true
			that.$request
				.request({
					url: that.api.live.my_live,
					method: 'GET',
					data: {
						page: that.page
					}
				})
				.then(res => {
					console.log(res); 
					if (that.page == 1) {
						that.userlist = res.list;
						that.page = that.page + 1;
					} else {
						if (res.list.length > 0) {
							that.userlist = that.userlist.concat(res.list); 
							that.page = that.page + 1;
						} else {
							if (that.page != 1) {
								if (!that.nomore) {
									that.nomore = true;
								}
							} else {
								that.nolist = true;
							}
						}
					}
					that.loading=false
					uni.hideLoading();
				});
		},
		tpinfo(e) {
			let id=e.currentTarget.dataset.id;
			let status=e.currentTarget.dataset.status;
			if(status==1){
				uni.navigateTo({
					url: '/pages/liveBroadcast/livePusher/pusher/pusher?id='+id
				});
			}else{
				uni.navigateTo({
					url: '/pages/liveBroadcast/livedetail/livedetail?id='+id
				});
			}
			
		}
	},
	onLoad() {
		that = this;
		that.getlist();
	},
	onReachBottom() {
		that.getlist();
	}
};
